.content-container {
  background-color: #fff;
  padding: 5rem 0rem 2rem 5rem;
  /* display: flex; */

  justify-content: center;
  align-items: right;
  margin-left: 12%;
  width: 88%;
  /* background-color: #fff; */
  /* background: url("/images/img-home.jpg") center center/cover no-repeat; */
  /* padding: 24px; */
}
.content-container2 {
  padding: 5rem 0rem 2rem 5rem;
  margin-left: 10%;
  width: 80%;

  justify-content: center;
  align-items: right;
}

.content-Title {
  display: flex;
  font-size: 24px;
  margin-bottom: 10px;
  color: #000000;
  padding: 0rem 5rem 0rem 0rem;
}

.content-heading {
  margin-bottom: 24px;
  font-size: 24px;
}
.content-note {
  font-family: Georgia;
  font-size: 20px;
  width: 100%;
  color: #000000;
  padding: 1rem 5rem 1rem 0rem;
}

.content-text {
  font-family: Georgia !important;
  font-size: 20px;
  width: 100%;
  color: #000000;
  /* padding-bottom: 24px; */
  padding: 1rem 5rem 5rem 0rem;
}
.math-text {
  font-family: "Georgia", serif !important; /* Be cautious with !important */
}

.content-image {
  background-color: #fff;
  margin-left: 12%;
  width: 88%; /* or any desired width */
  height: auto; /* maintain the aspect ratio */
  padding: 0 10rem 0 0rem;
}

.citation-container {
  /* font-family: monospace; */
  background-color: #f5f5f5;
  border: 1px solid #ccc;
  padding: 100px;
  margin: 10px;
}

.citation-code {
  white-space: pre-wrap;
  padding: 0rem;
  font-size: 18px;
  font-family: "Georgia" !important;
}
.citation-code pre {
  font-family: "Georgia", serif !important;
  white-space: pre-wrap; /* This will ensure that the lines wrap and not overflow */
  text-align: left;
  word-break: break-word; /* This will break long words if necessary */
  background-color: #e8e8e8;
  padding: 10px;
  /* padding: 0rem 5rem 5rem, 5rem; */
  overflow-wrap: break-word; /* This is another property that helps to break long words */
}

.content-text a:link,
.content-text a:visited,
.content-text a:hover,
.content-text a:active,
.content-text a:focus {
  color: blue; /* Replace #yourDesiredColor with the actual color code you want. */
  text-decoration: none; /* If you don't want underline on hover. */
}

@media screen and (max-width: 820px) {
  .footer-links {
    padding-top: 2rem;
  }

  .footer-input {
    width: 100%;
  }

  .btn {
    width: 100%;
  }

  .footer-link-wrapper {
    flex-direction: column;
  }

  .social-media-wrap {
    flex-direction: column;
  }
}

